//SPA and Routing with react router dom

function Home() {
  return <h2>Halaman depan</h2>
}

function ListView() {
  return (
    <div>
      <h2>Selamat malam </h2>
      <ul>
        <li><Link to ='user/hilman'>hilman</Link></li> 
        <li><Link to ='user/brandon'>brandon</Link></li>
      </ul>
    </div>
  
  )
}

function Nomatch() {
  return <h1>404,Halaman tidak ditemukan</h1>
}
function DetailView({match}) {
  return <h2> Ini halaman {match.params.name}</h2>
}
class App extends Component {
  render() {
    return (
      <BrowserRouter>
        <div>
          <nav>
            <li><Link to='/'>Home</Link></li>
            <li><Link to='/users'>User</Link></li>
          </nav>
          <main>
            <Switch>
              <Route path='/' exact component={Home} />
              <Route path='/users' exact component={ListView} />
              <Route path='/user/:name' exact component={DetailView} />
              <Route component={Nomatch} />
            </Switch>
          </main>
        </div>
      </BrowserRouter>
    )
  }
}
